import PayPal from "../components/Paypal";
import "../style/bootstrap-grid.min.css";
import "../style/Detail.scss";
import "../style/Momo.scss";
const { TabPane } = Tabs;
const { Option } = Select;
const { Step } = Steps;
const { TextArea } = Input;
const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};
