                        //res.data.DonateEnvent.soTienCanDonate = res.data.DonateEnvent.soTienCanDonate.replace(/\B(?=(\d{3})+(?!\d))/g, ".");
                        setDonate(res.data.DonateEnvent);
                        //console.log('das',res.data.DonateEnvent);
                        setImg(res.data.DonateEnvent.image)
    
