import React, { createRef, useState } from 'react';
import { Modal, Form, Input, Button, message } from 'antd';
import { LockOutlined, KeyOutlined } from '@ant-design/icons';
import '../style/Profile.scss';
import UserApi from '../Api/UserApi';
const Profile = () => {
    const [Error, setError] = useState("");
    /* const normFile = (e) => {

        if (Array.isArray(e)) {
            setFile(e.fileList[0].name)

        }
        return e && e.fileList;

    }; */
    const onFinish = (values) => {
        console.log('Success:', values);
        UserApi.putUpdateProfile(values)
        .then((res) => {
            if (res.data.status === "success") {
                message.success("Cp nht thng tin thnh cng !")
                localStorage.setItem("user", JSON.stringify(res.data.User));
                setTimeout(() => {
                    window.location.reload();
                }, 1000)
            }
            if (res.data.status === "error") {
                message.success("Cp nht thng tin thnh cng !")
                localStorage.setItem("user", JSON.stringify(res.data.User));
                setTimeout(() => {
                    window.location.reload();
                }, 1000)
            }
        })
        .catch((error)=>{
            console.log("Loi n: ",error);
        })
    };
    const [isModalVisible, setIsModalVisible] = useState(false);

    const showModal = () => {
        setIsModalVisible(true);
    };
    const form = createRef();
    const handleOk = () => {
        setIsModalVisible(false);
    };
    
    const handleCancel = () => {
        setIsModalVisible(false);
    };
    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };
   const onChangePass =( values)=>{
    UserApi.putChangePasss(values).then((res)=>{
            console.log(res);
            if (res.data.status === "success") {
                message.success("Thay i mt khu thnh cng !")
                setTimeout(() => {
                    handleCancel()
                }, 1000)
            }
        })
        .catch((error) => {
            console.log(error.response.data.error);
            setError(error.response.data.error)
        })

   }
    const user = JSON.parse(localStorage.getItem("user"))
    //console.log(user.phone);    
    return (
        <>
            <div className="row">
                <div className="col">
                    <div className="profile">
                        <Form
                            name="basic"
                            className="profile_form"
                           /*  labelCol={{ span: 8 }}
                            wrapperCol={{ span: 16 }} */
                            initialValues={{ username: `${user.username}`, fullName: `${user.fullName}`, email: `${user.email}`, id: `${user._id}` }}
                            onFinish={onFinish}
                            onFinishFailed={onFinishFailed} >
                            <Form.Item
                                name="id"
                                hidden>
                                <Input readOnly autoComplete={"off"} />
                            </Form.Item>
                            <Form.Item
                            label="Tn ti khon:"
                                name="username">
                                <Input readOnly autoComplete={"off"} />
                            </Form.Item>
                            <Form.Item
                            label="H v tn:"
                                name="fullName">
                                <Input autoComplete={"off"} />
                            </Form.Item>
                            <Form.Item
                            label="Email"
                                name="email">
                                <Input autoComplete={"off"} />
                            </Form.Item>
                            <Form.Item >
                                <Button onClick={showModal}> <KeyOutlined />i mt khu</Button>
                            </Form.Item>
                            <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                                <Button type="primary" htmlType="submit">
                                    Cp nht
                                </Button>
                            </Form.Item>
                        </Form>
                    </div>
                </div>
            </div>
            <Modal className="model_pass" footer={null} title="i mt khu" visible={isModalVisible} onCancel={handleCancel}>
                <Form
                    name="normal_resgiter"
                    className="resgiter-form"
                    initialValues={{ remember: true }}
                    onFinish={onChangePass}
                    ref={form}
                >
                    <p style={{ color: "red" }}>{Error}</p> 
                    <Form.Item
                        label="Mt khu c"
                        name="oldPassword"
                        rules={[
                            { required: true, message: 'Hy nhp mt khu!' },
                            { min: 8, message: 'Mt khu phi  8 k t' }
                        ]}
                    >
                        <Input
                            prefix={<LockOutlined className="site-form-item-icon" />}
                            type="password"

                            autoComplete="off"
                        />
                    </Form.Item>
                    <Form.Item
                        label="Mt khu mi"
                        name="newPassword"
                        rules={[
                            { required: true, message: 'Hy nhp mt khu!' },
                            { min: 8, message: 'Mt khu phi  8 k t' }
                        ]}
                    >
                        <Input
                            prefix={<LockOutlined className="site-form-item-icon" />}
                            type="password"

                            autoComplete="off"
                        />
                    </Form.Item>
                    <Form.Item
                        label="Nhp li mt khu"
                        name="passwordConfirm"
                        rules={[
                            {
                                required: true,
                                message: 'Hy nhp mt khu !',
                            },
                            ({ getFieldValue }) => ({
                                validator(_, value) {
                                    if (!value || getFieldValue('newPassword') === value) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject(new Error('Hai mt khu phi trng nhau !'));
                                },
                            }),
                        ]}>
                        <Input
                            prefix={<LockOutlined className="site-form-item-icon" />}
                            type="password"

                            autoComplete="off"
                        />
                    </Form.Item>
                    <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                        <Button type="primary" htmlType="submit">
                            i mt khu
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>

        </>

    );
}

export default Profile;
